
<template>
  <div>
    <h3>الأجهزة المرتبطة بالحساب</h3>

    <div class="card mb-3 p-3">
      <form @submit.prevent="create">
        <div class="mb-2">
          <label class="form-label"
            >تفعيل ميزة الرد التلقائى على الرسائل - عند التفعيل قم بإضافة
            نص</label
          >
          <input
            v-model="autoReply"
            class="form-control "
            placeholder="رساله الرد التلقائى"
          />
        </div>
        <div class="text-center">
           <button class="btn btn-primary" :disabled="creating">
          {{ creating ? "جار الإنشاء" : "إضافة جهاز جديد" }}
        </button>
        </div>
       
      </form>
    </div>

    <!-- Instances list -->
    <div class="card p-3 mb-3">
      
      <div v-if="loading">Loading...</div>
      <ul class="list-group" v-if="!loading">
        <div v-for="inst in instances" :key="inst.id" class="card">
          <div class="card-header bg-primary border-card">
            <h4 class="text-white text-center">الجهاز الرئيسى <br> الحالة: {{ inst.status == 'ready' ? 'متصل':'غير متصل' }}</h4>
          </div>
         
          <div class="card-footer bg-primary text-center">
            <div>
              <button
                class="btn btn-secondary me-2"
                @click="fetchQr(inst.instanceId)"
              >
                عرض الـQR
              </button>
              <button
                class="btn btn-success"
                @click="openSend(inst.instanceId)"
              >
                أرسال رسالة
              </button>

              <button
                class="btn btn-danger m-2"
                @click="logout(inst.instanceId)"
              >
                تسجيل خروج من الجهاز
              </button>
            </div>
          </div>
        </div>
      </ul>
    </div>

    <!-- QR Display -->
    <div v-if="qrBase" class="card p-3 mb-3 text-center">
      <h6>QR (image)</h6>
      <img
        :src="qrBase"
        alt="QR"
        style="width: 400px; height: 400px; object-fit: contain"
      />
    </div>

    <!-- Send Form -->
    <div v-if="showSend" class="card p-3">
      <h6>Send message (instance: {{ activeInstance }})</h6>
      <form @submit.prevent="sendMessage">
        <div class="mb-2">
          <label class="form-label">Phone (+country)</label>
          <input
            v-model="to"
            class="form-control"
            placeholder="+2010..."
            required
          />
        </div>
        <div class="mb-2">
          <label class="form-label">Message</label>
          <textarea v-model="msg" class="form-control" required></textarea>
        </div>
        <button class="btn btn-primary">Send</button>
        <button class="btn btn-link" @click.prevent="closeSend">Cancel</button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { ref, onMounted } from "vue";
import QRCode from "qrcode";

export default {
  setup() {
    const instances = ref([]);
    const loading = ref(false);
    const creating = ref(false);
    const autoReply = ref("");
    const qrBase = ref(null);
    const showSend = ref(false);
    const activeInstance = ref(null);
    const to = ref("");
    const msg = ref("");

    async function load() {
      loading.value = true;
      try {
        const r = await axios.get("/api/instances");
        instances.value = r.data.instances || r.data;
      } catch (err) {
        console.error(err);
      } finally {
        loading.value = false;
      }
    }
    async function logout(instanceId) {
      try {
        console.log(1231233);
        await axios.post(`/api/instances/${instanceId}/logout`);
        alert(`Instance ${instanceId} logged out ✅`);
        await load(); // reload instances list
      } catch (err) {
        alert(err.response?.data?.error || err.message);
      }
    }

    async function create() {
      creating.value = true;
      try {
        await axios.post("/api/instances", { autoReply: autoReply.value });
        await load();
      } catch (err) {
        alert(err.response?.data?.error || err.message);
      } finally {
        creating.value = false;
      }
    }

    async function fetchQr(instanceId) {
      try {
        const r = await axios.get(`/api/instances/${instanceId}/qr`);
        if (r.data && r.data.qr) {
          // ✅ generate QR image from string
          qrBase.value = await QRCode.toDataURL(r.data.qr);
        } else {
          qrBase.value = null;
          alert("No QR available");
        }
      } catch (err) {
        alert(err.response?.data?.error || err.message);
      }
    }

    function openSend(instanceId) {
      activeInstance.value = instanceId;
      showSend.value = true;
    }
    function closeSend() {
      showSend.value = false;
      activeInstance.value = null;
    }

    async function sendMessage() {
      try {
        await axios.post("/api/instances/send", {
          to: to.value,
          body: msg.value,
        });
        alert("Sent");
        closeSend();
      } catch (err) {
        alert(err.response?.data?.error || err.message);
      }
    }

    onMounted(load);

    return {
      instances,
      loading,
      create,
      creating,
      autoReply,
      fetchQr,
      qrBase,
      showSend,
      openSend,
      closeSend,
      activeInstance,
      to,
      msg,
      logout,
      sendMessage,
    };
  },
};
</script>